import React from 'react'
import EOSJS from 'eosjs'




class EOSComponent extends React.Component{
    constructor(props){
        super(props)

        this.state={
            projects:[{
                "key":"",
                "projectId":"",
                "projectOwner":"",
                "projectName":"",
                "projectStatus":"",
                "intro":"",                
                "quantityWanted":"",
                "quantityReceived":""
            }],
            donation:[{

            }]
        }
    }

   

    //componment加载完成后后执行Mount函数
    componentDidMount(){
        console.log('update project data...')
        let eosjsConfig={
            httpEndpoint:'http://api.kylin.eosbeijing.one:8880'
        }

        //let Eos=require('eosjs')

     //   let eosjs =Eos(eosjsConfig)

        // eosjs.getTabeRows({"scope":"wuxytestnet1","code":"wuxytestnet1","table":"project","json":true})
        // .then(result=>{
        //     let rows=result.rows
        //     this.setState({
        //         projects:rows
        //     })
        // })

    }

    donate(){
        console.log("donate...")
        let projecttoDonate=document.getElementById("projecttoDonate").value;  //视频里面是.value，但是这里没有value的选项；
        let contractSender=document.getElementById("contractSender").value;
        let privateKey = document.getElementById("privateKey").value;

        let eosjsConfig =  {
            chainId:"5fff1dae8dc8e2fc4d5b23b2c7665c97f9e9d8edf2b6485a86ba311c25639191",
            keyProvider:[privateKey],
            httpEndpoint:'http://kylin.meet.one:8888',
            expireInSeconds:60,
            broadcast:true,
            verbose:false,
            sign:true
        }

        let eosjs = EOSJS(eosjsConfig)    //new 一个eosjs对象
        
        //加载合约
        eosjs.contract('wuxytestnet1')
        .then((contract) => {
            console.log("加载合约成功")
            contract.offerlove(contractSender,projecttoDonate,{authorization:[contractSender]})  //类似与push action -p
            .then((res) => {
                console.log("捐赠成功")
            })
            .catch((err)=>{
                console.log("捐赠失败")
            })
        })

    }

    query(){
        console.log('query...')
        let contractSender=document.getElementById("contarctSender").value;
        let eosjsConfig={
            httpEndpoint:'http://api.kylin.eosbeijing.one:8880'
        }
        let eosjs =EOSJS(eosjsConfig)

        eosjs.getTabeRows({"scope":"wuxytestnet1","code":"wuxytestnet1","table":"donation","json":true})
        .then(result=>{
            let rows=result.rows
            let userDonations=[]
            rows.forEach(row => {
                if(row.account == contractSender){
                    userDonations[userDonations.length]=row
                }                
            });
            this.setState({
                donation:userDonations
            })
        })

    }


    render(){
        console.log("rendering...")
        return(
            <div style={{textAlign:'center'}}>
                {
                    this.state.projects.map(function(project,index){
                        return <div key={index}>
                            <h2>Project: {project.projectName}</h2>
                            <p>Prokect Id: {project.projectId}</p>
                            <p>Prokect Status: {project.projectStatus}</p>
                            <p>Prokect Wanted: {project.quantityWanted}</p>
                            <p>Prokect REceived: {project.quantityReceived}</p>
                            </div>
                    })
                }

                <form id="donate-form">
                    <p>请输入您要捐献的项目名称</p>
                    <input type="text" name="projecttoDonate"></input>
                    <p>请输入您的Eos账户名：</p>
                    <input type ="text" name="contractSender" id="contractSender"></input>
                    <p>请输入您的Eos账户Active私钥：</p>
                    <input type ="text" name="privateKey" id="privateKey"></input>
                    <p></p>
                    <button type="button" onClick={this.donate.bind(this)}>捐献</button>
                    <p></p>
                    <button type="button" onClick={this.query.bind(this)}>查询</button>
                </form>
                <h2>捐赠记录</h2>
                {
                    this.state.donation.map(function(project,index){
                        return <div key={index}>
                            <p>{project.projectName}</p>
                            </div>
                    })
                }
            </div>
        );
    }
}





export default EOSComponent